<template>
  <div id="app">
        <div class="card2">
               <div class="card2_top">
                 <el-form :inline="true" :model="form" class="demo-form-inline">
                  <div style="float: left; margin-left:10%; margin-top: 20px;">
                      <el-form-item label="关键字" >
                          <el-input v-model="form.powerPlantName" placeholder="请输入关键字" id="btn0"></el-input>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="primary" @click='search()'>查询</el-button>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="info" @click='reback'>重置</el-button>
                        </el-form-item>
                  </div>
                   <div style="float: right; margin-right:10%; margin-top: 20px;">
                      <el-form-item>
                          <el-button type="success" @click='add'><router-link to='./anliku'> +新增</router-link></el-button>
                        </el-form-item>
                   </div>
                 </el-form>
               </div>
               <div id="card2_bottom">
                 <el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%"
                   @selection-change="handleSelectionChange" >
                   <el-table-column prop="powerPlantName" label="文档名称">
                   </el-table-column>
                   <el-table-column prop="powerPlantType" label="附件">
                   </el-table-column>
                   <el-table-column prop="power" label="上传时间">
                    </el-table-column>
                   <el-table-column label="操作">
                     <template slot-scope="scope">
                       <el-button @click="handleClick(scope.row)" type="text" size="small" :data='list.id' >删除
                       </el-button>
                       <el-button @click="handleClick2(scope.row),Show()" type="text" size="small" >修改
                       </el-button>
                     </template>
                   </el-table-column>
                 </el-table>
                 <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]"
                   :page-size="pagesize" :total="list.length" @current-change="handleCurrentChange"
                   @size-change="handleSizeChange">
                 </el-pagination>
               </div>
             </div>
  </div>
</template>

<script>
export default {
  name: '',
  data() { 
    return {
        isShow:false,
        
          form: {
            name: '',
            region: '',
  
          },
          formInline: {
            user: '',
            region: ''
          },
        powerPlantType: '',
        generatorCapacity: '',
        voltageLevel: '',
        province: '',
        form: {
          powerPlantName: '',
          address: '',
          phone: '',
          postcode: '',
        },
        msg: 8888,
        list3:[

        ],
        list2:[

        ],
        list:[
            {
                powerPlantName: 'kang+',
                powerPlantType: 'kjh',
                power:'2019-9-09',
                power1:'dicenggongren'
            },
            {
              powerPlantName: 'hanhan',
                powerPlantType: 'lilei',
                power:'2019-9-16',
                power1:'hanhan'
            }
        ],
        pagesize: 5,
        currpage: 1
      }
  }
 }
</script>

<style lang="" scoped>
 .back{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    background-color:rgba(0,0,0,0.5);
    z-index: 2;
  }
  .el-input {
    width: 150px;
    height: 25px;
  }

  .el-select {
    width: 150px;
    height: 25px;
  }

  .el-form-item {
    margin-bottom: 10px;
  }

  .el-button {
    padding: 4px 8px;
  }


  .card2 {
    text-align: center;
    width: 97%;
    margin: auto;
    background: white;
  }

  .card1 {
    width: 550px;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
    background: white;
  }
  .el-form-item__label{
    width: 100px !important;
    padding: 0 !important;
  }
  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }


  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style{
    width: 120px;
  }

</style>


